"use strict"

//Variables --------------------------------------------------------------------------

//Rest API url
const pfURL = "https://devlie.se/dt173g%20-%20Webbutveckling%20III/Projekt%20-%20CV/API/portfolio.php";

//List
const portfolioEl = document.getElementById("portfolio");

//Eventlisteners
window.addEventListener("load", getPortfolio);




//Get Portfolio
function getPortfolio () {
    portfolioEl.innerHTML = "";

    //Fetch request
    fetch(pfURL)
        .then(response => response.json())
        .then(data => {
            console.log(data);

            //Table headers
            portfolioEl.innerHTML +=
            `
            <tr>
                <th>Title:</th>
                <th>Link:</th>
                <th>Description:</th>
            </tr>
           
            `

            //Table content data
            data.forEach(pf => {
                portfolioEl.innerHTML +=
                 `
                 <tr >
                    <td> ${pf.title} </td>
                    <td> <a href=" ${pf.url} ">Link</a></td>
                    <td> ${pf.description} </td>

                </tr>
                
                 `
            })
        })
}