"use strict"

//Variables --------------------------------------------------------------------------

//Rest API url
const workURL = "https://devlie.se/dt173g%20-%20Webbutveckling%20III/Projekt%20-%20CV/API/work.php";

//List
const workEl = document.getElementById("work");

//Eventlisteners
window.addEventListener("load", getWork);


//Get Work
function getWork () {
    workEl.innerHTML = "";

    //Fetch request
    fetch(workURL)
        .then(response => response.json())
        .then(data => {
            console.log(data);

            //Table headers
            workEl.innerHTML +=
            `
            <tr>
                <th>Company:</th>
                <th>Title:</th>
                <th>Start Date:</th>
                <th>End Date:</th>
            </tr>
           
            `

            //Table content data
            data.forEach(work => {
                workEl.innerHTML +=
                 `
                 <tr>
                    <td> ${work.company} </td>
                    <td> ${work.title} </td>
                    <td> ${work.startDate} </td>
                    <td>${work.endDate}</td>
                </tr>
                
                 `
            })
        })
}