"use strict"

//Variables --------------------------------------------------------------------------

//Rest API url
const edURL = "https://devlie.se/dt173g%20-%20Webbutveckling%20III/Projekt%20-%20CV/API/education.php";

//List
const edEl = document.getElementById("education");

//Eventlisteners
window.addEventListener("load", getEd);



//Get Education
function getEd () {
    edEl.innerHTML = "";

    //Fetch request
    fetch(edURL)
        .then(response => response.json())
        .then(data => {
            console.log(data);

            //Table headers
            edEl.innerHTML +=
            `
            <tr>
                <th>School:</th>
                <th>Course name:</th>
                <th>Start Date:</th>
                <th>End Date:</th>
            </tr>
           
            `

            //Table content data
            data.forEach(course => {
                edEl.innerHTML +=
                 `
                 <tr >
                    <td> ${course.school} </td>
                    <td> ${course.name} </td>
                    <td> ${course.startDate} </td>
                    <td>${course.endDate}</td>

                </tr>
                
                 `
            })
        })
}